@import "./font.css";


body {
  font-family: Cairo, sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: normal;
  margin: 0;
  color: var(--primary-black-color);
}

@media screen and (max-width: 768px) {
  body {
    font-size: 16px;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Variables */

:root {
  --primary-color: #E76F51;
  --primary-dark-color: #c15c43;

  --secondary-color: #264653;
  --secondar-dark-color: #172a31;

  --primary-blue-color: #1E2A41;

  --primary-black-color: #0D1729;

  --primary-yellow-color: #E9C369;

  --gray-hover-color: rgba(0,0,0,.075);
  --gray-bg-color: #fbfbfb;
  --unseen-gray-color: rgba(0,0,0,.1);

  --danger-color: #dc3545;

  --text-color: #4f4c4c;
}


/* Bootstrap Overrides */

.alert {
  text-align: right;
}

.text-primary {
  color: var(--primary-color) !important;
}

.text-secondary {
  color: var(--secondary-color) !important;
}

.form-group .text-muted {
  margin-top: -5px;
  margin-bottom: 10px;
}

.form-control {
  font-size: 16px;
  color: black;
}

.form-control-lg {
  height: 60px;
}

.alert-default {
  color: white;
  background-color: var(--secondary-color);
  border-color: var(--secondary-color);
}

.alert-danger {
  color: white;
  background-color: var(--danger-color);
  border-color: var(--danger-color);
}

.alert-info {
  color: white;
  background-color: var(--primary-color);
  border-color: var(--primary-color);
}

.alert-warning {
  color: white;
  background-color: var(--primary-yellow-color);
  border-color: var(--primary-yellow-color);
}

.btn {
  border-radius: 4px;
}

.btn-primary {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  color: white;
  font-weight: 600;
}

.btn-primary:hover, .btn-primary:focus, .btn-primary:active, .btn-primary:visited {
  background-color: var(--primary-dark-color) !important;
  border-color: var(--primary-dark-color) !important;
}

.btn-primary.disabled, .btn-primary:disabled {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  color: white;
  cursor: not-allowed;
}

.btn-secondary {
  background-color: var(--secondary-color);
  border-color: var(--secondary-color);
  color: white;
  font-weight: 600;
}

.btn-secondary:hover, .btn-secondary:focus, .btn-secondary:active, .btn-secondary:visited {
  background-color: var(--secondar-dark-color) !important;
  border-color: var(--secondar-dark-color) !important;
}

.btn-secondary.disabled, .btn-secondary:disabled {
  background-color: var(--secondar-dark-color);
  border-color: var(--secondar-dark-color);
  color: white;
  cursor: not-allowed;
}

.btn-outline-primary {
  border-color: var(--primary-color);
  color: var(--primary-color);
  font-weight: 600;
}

.btn-outline-primary:hover, .btn-outline-primary:focus, .btn-outline-primary:active, .btn-outline-primary:visited {
  border-color: var(--primary-color) !important;
  background-color: var(--primary-color) !important;
  color: white;
}

.badge-primary {
  background-color: var(--primary-color);
}

.badge-secondary {
  background-color: var(--secondary-color);
}

.modal-header .close {
  float: left;
  margin: -1rem auto -1rem -1rem;
}

.rdtPicker td.rdtActive, .rdtPicker td.rdtActive:hover {
  background-color: var(--primary-color);
}



/* Common Styles */

.color-primary {
  color: var(--primary-color);
}

.color-secondary {
  color: var(--secondary-color);
}

.color-danger {
  color: var(--danger-color) !important;
}

.nav-pills {
  display: flex;
  justify-content: center;
}

.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
  background-color: var(--primary-color);
}

@keyframes back-link-animation {
  from {font-size: 2rem;}
  to {font-size: 2.5rem;}
}

.back-link {
  vertical-align: middle;
  margin-left: 10px;
  cursor: pointer;
  color: var(--primary-color);
}

.back-link:hover {
  color: var(--primary-dark-color);
}

.form {
  max-width: 500px;
  margin: auto;
  text-align: right;
}

.form-container {
  padding: 30px 20px;
}

.page-content {
  background: var(--gray-bg-color);
  padding: 25px 0;
}

@media screen and (max-width: 768px) {
  .form-container {
    padding: 30px 5px;
  }

  .page-content {
    padding: 20px 15px;
  }
}

.page-content-block {
  text-align: right;
  margin: 50px auto;
}

.page-title {
  margin-bottom: 30px;
  font-weight: 600;
}

@media screen and (max-width: 768px) {
  .page-content-block {
    margin: 20px auto;
  }

  .page-title {
    font-size: 22px;
  }
}

.section-title {
  width: 100%;
  text-align: right;
  font-size: 35px;
  font-weight: 600;
  margin-bottom: 50px;
}

.section {
  padding: 50px 0;
  border-bottom: 1px solid var(--unseen-gray-color);
}

@media screen and (max-width: 768px) {
  .section {
    padding: 0;
  }
}

.section-block {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: right;
}

.bg-gray {
  background: var(--gray-bg-color);
}

.bg-white {
  background: white;
}

.block-title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 40px;
  font-weight: 700;
}

@media screen and (max-width: 768px) {
  .block-title {
    margin-bottom: 10px;
    font-size: 20px;
  }
}

.block-title i {
  font-size: 35px;
  margin-left: 10px;
  color: var(--secondary-color);
}

a, .text-link, .link, .btn-link {
  color: var(--primary-color);
  text-decoration: none;
  background-color: transparent;
  cursor: pointer;
}

.link:active, .link:active.link:hover, .link:active.link:focus {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  color: white;
}

a:hover, .text-link:hover, .link:hover, .link:focus, .btn-link:hover, .btn-link:active, .btn-link:focus {
  color: var(--primary-color);
  text-decoration: none;
}

.text-link.underline:hover {
  text-decoration: underline;
}

.loading-spinner-container {
  width: 100%;
}

.loading-spinner.large {
  width: 3rem;
  height: 3rem;
}




/* Atoms */

/* Image Icon */

.image-icon-container {
  margin-left: 5px;
}

.image-icon {
  width: 45px;
}





/* Site Header */

.site-header {
  border-bottom: 1px solid var(--unseen-gray-color);
}

.site-header .container .row {
  position: relative;
}

@media screen and (max-width: 768px) {
  .site-header .container .row {
    padding-top: 0.5rem !important;
    padding-bottom: 0.5rem !important;
  }
}

.logo {
  width: 40px;
}

@media screen and (max-width: 768px) {
  .logo {
    width: 30px;
  }
}

.logo-container {
  align-items: center;
}

.slogan {
  font-weight: bold;
  margin-right: 10px;
}

.header-profile-container {
  display: flex;
  align-items: center;
  position: absolute;
  left: 10px;
  top: 40px;
}

@media screen and (max-width: 768px) {
  .header-profile-container {
    top: 25px;
  }
}



/* Site Footer */

.site-footer {
  background: var(--secondary-color);
  color: white;
  font-size: 14px;
}



/* Home Page */

#home-page .page-content {
  padding: unset;
}



/* Introduction Section */

.introduction-text {
  padding: 0 150px;
  margin-bottom: 50px;
  font-weight: 600;
}

@media screen and (max-width: 768px) {
  .introduction-text {
    padding: 0 30px;
  }

  .user-segment:last-of-type {
    margin-top: 30px;
  }
}

.introduction-segment-img {
  width: 200px;
  height: 180px;
}

.introduction-segment-btn {
  width: 100%;
  height: 60px;
  margin: 20px 0;
}


/* How To Section */

.how-img {
  width: 200px;
}



/* Registration Page */

#registration-page .page-content {
  background: var(--gray-bg-color);
}

.registration-question {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 500px;
  margin: 10px auto;
  text-align: right;
}

.registration-question i {
  color: #e76e50;
  font-size: 27px;
  margin-left: 5px;
}



/* Teacher Dashboard Page */

.class-card {
  margin: 10px auto;
}

.class-card .btn-dropdown-options {
  position: absolute;
  top: 10px;
  left: 10px;
}

.class-card .class-name {
  font-weight: 600;
  margin-bottom: 20px;
}

.card-text-bold {
  font-weight: 700;
}

.class-card .card-text {
  font-size: 16px;
  margin: 0;
}

.btn-class-details {
  margin-top: 20px;
}

@media screen and (max-width: 768px) {
  .class-card .class-name {
    font-size: 18px;
    margin-bottom: 10px;
  }

  .btn-class-details {
    width: 100%;
  }
}

.btn-add-class {
  margin-right: 10px;
}

.code-badge {
  font-size: 1rem;
  font-weight: 600;
}

@media screen and (min-width: 768px) {
  .add-class-modal .modal-body {
    padding: 20px 50px 30px 50px;
  }
}


/* CMS Class Page */

.class-details-container {
  text-align: center;
}

.class-details {
  background: #f7f7f7;
  border: 1px solid #e5e5e5;
  padding: 10px 20px 0 20px;
  display: inline-block;
  margin: 0 0 30px;
  border-radius: 8px;
}

.detail-point {
  margin: 0 5px;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.detail-point-icon {
  color: var(--primary-blue-color);
  font-size: 20px;
}

.detail-point-label {
  font-size: 14px;
}

.detail-point-value {
  border: 1px solid white;
  padding: 5px;
  background: #254653;
  width: 100%;
  border-radius: 8px;
  font-weight: 700;
  font-size: 14px;
  color: white;
}

@media screen and (max-width: 768px) {
  .detail-point-label {
    font-size: 12px;
  }

  .detail-point-value {
    font-size: 12px;
  }
}



.tasks-container {
  padding: 0 20px;
}

.tasks {
  margin-top: 50px;
}

@media screen and (max-width: 768px) {
  .tasks {
    margin-top: 0;
  }
}

.tasks .date-tasks-group {
  margin: 25px 0;
  width: 90%;
}

.date-tasks-group i {
  margin-left: 7px;
  color: var(--secondary-color);
}

@media screen and (max-width: 768px) {
  .tasks .date-tasks-group {
    margin: 25px auto;
  }
}

.tasks .tasks-date-title {
  font-weight: 600;
  margin-bottom: 20px;
}

@media screen and (max-width: 768px) {
  .tasks .tasks-date-title {
    font-size: 18px;
  }
}

.task-card {
  margin: 15px 0;
}

.task-card .card-body {
  padding: 15px;
}

@media screen and (max-width: 768px) {
  .task-card .card-body {
    padding: 10px;
  }
}

.task-card .task-content-container {
  display: inline-flex;
  align-items: center;
}

.task-card .task-content {
  margin: 0;
}

@media screen and (max-width: 768px) {
  .task-card .task-content {
    font-size: 14px;
  }
}

.task-card .card-checkbox {
  margin-left: 10px;
}

.task-card .actions {
  display: inline;
  float: left;
}

.task-card .action-icon {
  cursor: pointer;
  color: var(--primary-color);
  margin: 0 7px;
}

.task-card .students-completed-count {
  font-size: 14px;
  margin: 0;
}

.task-card .students-completed-count .badge {
  font-size: 16px;
}

.task-card.teacher .card-footer {
  padding: 5px 20px;
}

.task-card.student .card-footer {
  padding: 5px 20px;
}

.task-card.student .card-footer-text {
  margin: 0;
  font-size: 14px;
}

@media screen and (max-width: 768px) {
  .task-card.student .card-footer-text {
    font-size: 12px;
  }
}

.task-date-picker .rdtPicker {
  position: relative;
  margin: auto;
  width: 100%;
}

.task-date-picker input {
  display: none;
}


/* Custom Checkbox With Animation */

@keyframes check {0% {height: 0;width: 0;}
  25% {height: 0;width: 10px;}
  50% {height: 20px;width: 10px;}
}
.checkbox{background-color:#fff;display:inline-block;height:28px;margin:0 .25em;width:28px;border-radius:4px;border:1px solid #ccc;float:right;cursor:pointer}
.checkbox span{display:block;height:28px;position:relative;width:28px;padding:0}
.checkbox span:after{-moz-transform:scaleX(-1) rotate(135deg);-ms-transform:scaleX(-1) rotate(135deg);-webkit-transform:scaleX(-1) rotate(135deg);transform:scaleX(-1) rotate(135deg);-moz-transform-origin:left top;-ms-transform-origin:left top;-webkit-transform-origin:left top;transform-origin:left top;border-right:4px solid #fff;border-top:4px solid #fff;content:'';display:block;height:20px;left:3px;position:absolute;top:15px;width:10px}
.checkbox span:hover:after{border-color:#999}
.checkbox input{display:none}
.checkbox input:checked + span:after{-webkit-animation:check .8s;-moz-animation:check .8s;-o-animation:check .8s;animation:check .8s;border-color:#555}
.checkbox input:checked + .default:after{border-color:#444}
.checkbox input:checked + .primary:after{border-color:var(--primary-color)}
.checkbox input:checked + .secondary:after{border-color:var(--secondary-color)}
.checkbox input:checked + .success:after{border-color:#8bc34a}
.checkbox input:checked + .info:after{border-color:#3de0f5}
.checkbox input:checked + .warning:after{border-color:#FFC107}
.checkbox input:checked + .danger:after{border-color:var(--danger-color)}
